
:root {
	--color-grey-dark: #5f5f5f;
	--color-grey-light: #D9D9D9;
	--color-white: white;
	--color-darkblue: rgb(0, 5, 41);
}



/* HEADING */

.subHeadingGrid{
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  background: white;
  width: 500px;
  margin: auto;
  color: black;
  border-radius: 10px;
}

.teamgridheading{
  border-radius: 10px;
  background: var(--color-grey-light);
  border-radius: 10px;
  border-color: white;
  margin: 5px 0px 5px 0px;
  font-size: 15px;
  display: inline-grid;
}

.subHeading{
  text-align: center;
}

.subheading1{
  font-size: 20px;
  line-height: 1.5em;
  margin: 0px 0px 0px 0px;
}

.subheading2{
    font-size: 16px;
    font-weight: 400;
    margin: 0px;
}

.subheading3{
    font-size: 16px;
    font-weight: 400;
    margin: 40px 0px 0px 0px;
}

.subheading4{
    font-size: 16px;
    font-weight: 400;
    margin: 5px 0px 10px 0px;
}

.subHeadingGridContent{
  font-size: 15px; 
  line-height: 1.5em;
  font-weight: 100;
  margin: 20px auto 0px auto;
}

.mainHeading{
  background-color: var(--color-darkblue);
  color: white;
  padding: 50px 0px 50px 0px;
  text-align: center;
  text-transform: capitalize;
}

.col1{
  padding: 0px 0px 0px 20px;
  font-size: 20px;
}

.headingTitle{
  margin: 20px auto 20px auto;
  text-align: center;
  color: var(--color-darkblue);
}


.sublist{
  margin: 0px 0px 0px 50px;

}


ol {
  list-style: none;
  counter-reset: counter-function;
}

li {
  counter-increment: counter-function;
  margin: 0.25rem;
  display: flex;
  align-items: center; /* adjust as needed */
}

li::before {
  content: counter(counter-function);
  background: var(--color-grey-dark);
  flex: 0 0 2rem;
  height: 2rem;
  border-radius: 50%;
  line-height: 2rem;
  color: white;
  text-align: center;
  margin-right: 1rem; /* adjust as needed */
  align-self: baseline;
}

.sublistitem::before {
  content: counter(counter-function);
  background: var(--color-grey-light);
  flex: 0 0 2rem;
  height: 2rem;
  border-radius: 50%;
  line-height: 2rem;
  color: black;
  text-align: center;
  margin-right: 1rem; /* adjust as needed */
}

/* SECTIONS */

.sectionTitle{
  text-align: center;
  padding: 15px 0px 15px 0px;
  background: var(--color-grey-light);
  border-radius: 15px;
}

.content{
  margin: 30px 0px 0px 0px;
}

.section{
  margin: 50px 20px 20px 20px;
}

.justifyAlign{
  text-align: justify;
}




/* HOW IT WORKS */

.how-it-works{
  padding: 50px 40px 50px 40px;

}

.heading{
  padding: 60px 0px 50px 0px;
  text-align: center;
  text-transform: capitalize;
}

.info-box{
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0px 10px 50px 10px;
  padding-top: 30px;
}

.info-box img{
  width: 300px;
  height: 300px;
  background: black;
  border-radius: 10px;
}

.text{
  margin: auto;
  padding: 10px 0px 10px 0px;
  width: 90%;
  text-align: center;
}

.info-heading{
  font-weight: 800;
  font-size: 25px;
  text-align: center;
  margin-bottom: 20px;
  text-transform: capitalize;
}

.info-description{
  font-weight: 400;
  font-size: 15px;
  /* text-align: center; */
  margin-bottom: 4px;
}






/* WHY US */
.whyUsGrid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  padding-top: 30px;
  max-width: 1300px;
}

.horizontal-center{
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.whyUsBox{
  position: relative;
  padding: 0px 0px 50px 0px;
}

.whyUsImg{
  background: black;
  height: 150px;
  width: 150px;
  border-radius: 10px;
}

.whyUsHeading{
  margin-top: 30px;
  color: black;
}

.whyUsDescription{
  padding-top: 50px;
  color: black;
  max-width: 180px;
}



/* REFERENCES */

.refGrid{
  grid-template-columns: 0.1fr 2fr;
  grid-template-rows: 1fr 1fr 1fr;
  display: inline-grid;
  align-content: center;
  margin: 50px;
  row-gap: 20px;
}





/* FOOTER */

.footer {
  background: var(--color-darkblue);
  color: var(--color-white);
  padding: 20px 0px 20px 0px;
  font-size: 15px;
  text-align: center;
}